import Vue from 'vue'
import VueRouter from 'vue-router'
import Dinghy from '@/components/Dinghy.vue'
import Home from '@/views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/dinghy',
    name: 'dinghy',
    component: Dinghy
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
